#contact-me {
  height: 250px;
  margin-top: 15vh;
  width: 90%;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

#about-me-title {
  font-size: 1.6em;
  font-weight: 800;
  font-family: Ubuntu-Light;
}

#about-me {
  margin-left: 2.5vw;
  display: inline-block;
}

#about-me-text {
  display: inline-block;
  margin-top: 5vh;
}

#profile-pic {
  max-width: 40vw;
  max-height: 40vh;
  width: 15em;
  height: 15em;
  object-fit: cover;
  object-position: right;
  display: inline;
  right: 0px;
  margin-top: 30px;
}

#about-me-summary {
  width: 40vw;
  margin-right: 5vw;
}

#contact-me-title {
  font-weight: 300;
}

#fcc-link {
  padding-bottom: 10vh;
}

p {
  font-family: Ubuntu-Light;
}

@font-face {
  font-family: "Ubuntu-Light";
  src: local("Ubuntu-Light"),
    url("../fonts/Ubuntu-Light.ttf") format("truetype");
  font-weight: normal;
}

@media (min-width: 700px) {
  #about-me-summary {
    width: 30vw;
  }
}
