$textColor: white;

#nav-bar {
  color: $textColor;
  background: black;
  width: 100vw;
  height: 10vh;
  left: 0px;
  top: 0px;
  position: fixed;
  display: flex;
  align-items: center;
  z-index: 1;
  font-family: Montserrat-Medium;
  font-size: 0.75em;
  text-transform: uppercase;
}

@font-face {
  font-family: "DelaGothicOne-Regular";
  src: local("DelaGothicOne-Regular"),
    url("../fonts/DelaGothicOne-Regular.ttf") format("truetype");
  font-weight: normal;
}

@font-face {
  font-family: "Montserrat-Medium";
  src: local("Montserrat-Medium"),
    url("../fonts/Montserrat-Medium.ttf") format("truetype");
  font-weight: normal;
}

#links-section {
  margin-left: 10%;
  margin-right: auto;
  margin-bottom: auto;
  margin-top: auto;
}


.nav-links {
  text-decoration: none;
  color: $textColor;
  font-size: 1.25em;
  width: 100vw;
}

.nav-links:link {
  text-decoration: none;
  color: $textColor;
}

.nav-links:hover {
  text-decoration: none;
  color: $textColor;
}

.nav-links:visited {
  text-decoration: none;
  color: $textColor;
}

.nav-links:active {
  text-decoration: none;
  color: $textColor;
}
