#welcome-section {
  margin-top: 50vh;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

#intro {
  font-family: Boogaloo;
  font-size: 4em;
}

#name {
  font-family: Boogaloo;
  font-size: 1.5em;
}

#personal-summary {
  font-family: Ubuntu-Light;
  width: 70%;
  max-width: 350px;
}

#work-button {
  border: 2px solid black;
  font-family: Ubuntu-Light;
  margin-bottom: 80px;
}

@font-face {
  font-family: "Boogaloo-Regular";
  src: local("Boogaloo-Regular"),
    url("../fonts/Boogaloo-Regular.ttf") format("truetype");
  font-weight: normal;
}

@font-face {
  font-family: "Ubuntu-Light";
  src: local("Ubuntu-Light"),
    url("../fonts/Ubuntu-Light.ttf") format("truetype");
  font-weight: normal;
}

@media (min-width: 700px) {
  #welcome-summary {
    margin-left: 8vw;
  }
}
